import React from "react";
import DetailPresenter from "./DetailPresenter";
import { movieApi, tvApi } from "../../api";

export default class extends React.Component {
  // ìƒì„±ì. í•´ë‹¹ ì»´í¬ë„ŒíŠ¸ê°€ ì‹¤í–‰ë˜ë©´ ìµœì´ˆë¡œ ì‹¤í–‰
  constructor(props) {
    super(props);

    const {
      location: { pathname }
    } = props;

    this.state = {
      result: null,
      error: null,
      loading: true,
      isMovie: pathname.includes("/movie/")
    };
  }

  async componentDidMount() {
    // Routerê°€ ê¸°ë³¸ì ìœ¼ë¡œ Routeë“¤ì—ê²Œ ì „ë‹¬í•˜ëŠ” props
    // :idë¥¼ ì–»ì„ ìˆ˜ ìˆë‹¤.
    const {
      match: {
        params: { id }
      },
      history: { push }
    } = this.props;

    const { isMovie } = this.state;
    const numberId = Number(id);

    if (Number.isNaN(numberId)) {
      alert("ìƒì„¸í˜ì´ì§€ì— ì ‘ê·¼í•  ìˆ˜ ì—†ìŠµë‹ˆë‹¤.");
      // componentDidMount í•¨ìˆ˜ë¥¼ ì¢…ë£Œì‹œí‚¤ê¸° ìœ„í•´ return;
      return push("/");
    }
    let { result } = this.state;
    try {
      if (isMovie) {
        // let ë³€ìˆ˜ë¥¼ í™œìš©í•œ ê°ì²´ ë¹„êµ¬ì¡°í™”.
        // ì „ì²´ êµ¬ë¬¸ì„ ()ë¡œ ê°ì‹¼ë‹¤.
        ({ data: result } = await movieApi.detail(numberId));
      } else {
        // ìœ„ì˜ êµ¬ë¬¸ê³¼ ê°™ë‹¤.
        const { data } = await tvApi.detail(numberId);
        result = data;
      }
    } catch (e) {
      console.log(e);
      this.setState({ error: "Can't find detail info" });
    } finally {
      this.setState({ loading: false, result });
    }
  }

  render() {
    const { result, error, loading } = this.state;
    return <DetailPresenter result={result} error={error} loading={loading} />;
  }
}
